// DogStaDetail.js -     
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useHistory } from "react-router";
import { useSelector, useDispatch } from "react-redux";

// 
import TopBar from "../../components/TopBar";
import NavBar from "../../components/NavBar";
import CommentList from "../../components/DogstaComment/CommentList";
import CommentWrite from "../../components/DogstaComment/CommentWrite";
import SuccessModal from "../../components/Modal/SuccessModal";
import Spinner from "../../shared/Spinner";

// 
import { actionCreators as dogstaActions } from "../../redux/modules/dogsta";
import { actionCreators as commentActions } from "../../redux/modules/comment";

// 
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import CommentOutlinedIcon from "@mui/icons-material/CommentOutlined";
import MailOutlineIcon from "@mui/icons-material/MailOutline";

const DogStaDetail = (props) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const dog = localStorage.getItem("checkDog")
  const dispatch = useDispatch();
  const history = useHistory();

  const post = useSelector((state) => state.dogsta.eachList); //    
  const postId = props.match.params.dogPostId; //    
  const currentPostUserId = props.match.params.userId; //     
  const userId = localStorage.getItem("userId"); //     
  const userImage = localStorage.getItem("image"); //     

  const likeCnt = useSelector((state) => state.dogsta.likeCnt); //   
  const myLike = useSelector((state) => state.dogsta.likeExist); //   
  const commentList = useSelector((state) => state.comment.commentList); //  

  //  ,  
  const [liked, setLiked] = useState(Boolean);
  const [likeCount, setLikeCount] = useState();

  const [modal, setModal] = useState();
  const [loading, setLoading] = useState();
  const toggleLike = () => {
    if (liked === true) {
      setLiked(false);
      setLikeCount(likeCount - 1);
      if (likeCount < 0) {
        return;
      }
    } else {
      setLiked(true);
      setLikeCount(likeCount + 1);
    }
    dispatch(dogstaActions.toggleLikeMD(postId, liked));
  };

  const editPost = () => {
    history.push(`/dogStaEdit/${currentPostUserId}/${postId}`);
  };

  const deletePost = () => {
    setLoading(true);
    dispatch(dogstaActions.deletePostMD(postId)); //  
  };
  const getModal = useSelector((state) => state.dogsta.modal);
  useEffect(() => {
    dispatch(dogstaActions.getPostMD(currentPostUserId, postId)); //     
    dispatch(dogstaActions.getLikesMD(postId)); //    
    dispatch(dogstaActions.getMyLikeMD(postId));
    dispatch(commentActions.getCommentMD(postId));
    setLiked(myLike);
    setLikeCount(likeCnt);
  }, [myLike, likeCnt, postId]);

  if (loading) {
    return <Spinner />;
  }
  return (
    <>
      <Wrap>
        {/*   +   +  */}
        <TopBar>{post.userNickname} </TopBar>

        {/*   - ,*/}
        <UserInfo
          onClick={() => {
            history.push(`/mypage/${post.userId}`);
          }}
        >
          <UserImg src={post.userImage} />
          <span>{post.userNickname}</span>
        </UserInfo>

        {/*   */}
        <Write>
          {/*   */}
          <ImageWrap>
            <PostImage src={post.dogPostImage} />
          </ImageWrap>

          {/*   */}
          <PostInfo>
            <Left>
              <LikeCnt>
                {/*    */}
                {liked ? (
                  <FavoriteIcon
                    onClick={toggleLike}
                    sx={{ fontSize: 24 }}
                    style={{
                      color: "red",
                      verticalAlign: "middle",
                      marginRight: "4px",
                    }}
                  />
                ) : (
                  <FavoriteBorderIcon
                    onClick={toggleLike}
                    sx={{ fontSize: 24 }}
                    style={{ verticalAlign: "middle", marginRight: "4px" }}
                  />
                )}
                {likeCount}
              </LikeCnt>

              {/*  */}
              <CommentCnt>
                <CommentOutlinedIcon
                  sx={{ fontSize: 24 }}
                  style={{ verticalAlign: "middle", marginRight: "4px" }}
                />
                {commentList.length}
              </CommentCnt>

              {/*       ,      */}
              {currentPostUserId !== userId && (
                <MailOutlineIcon
                  sx={{ fontSize: 24 }}
                  onClick={() => {
                    history.push(`/chatwrite/${currentPostUserId}`);
                  }}
                  style={{ verticalAlign: "bottom", cursor: "pointer" }}
                />
              )}
            </Left>

            {/*     =          */}
            <Right>
              {currentPostUserId === userId && (
                <BtnWrap>
                  <button onClick={editPost}></button>
                  <span>|</span>
                  <button onClick={deletePost}></button>
                </BtnWrap>
              )}
            </Right>
          </PostInfo>
          <PostDesc>{post.dogPostDesc}</PostDesc>
          <Time>{post.AGOTIME}</Time>
        </Write>

        {/*  */}
        <CommentWrap>
          {commentList[0] ? (
            <div>
              {commentList.map((comment, index) => {
                return (
                  <div>
                    <CommentList comment={comment} key={index} />
                  </div>
                );
              })}
            </div>
          ) : (
            <div>  .</div>
          )}
        </CommentWrap>
       { <CommentWrite userId={userId} postId={postId} userImage={userImage} />} 
      </Wrap>
      <NavBar add_dogsta />
    </>
  );
};

const Wrap = styled.div`
  padding: 0 5%;
`;
const UserInfo = styled.div`
  display: flex;
  justify-content: left;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  cursor: pointer;
  span {
    font-weight: 600;
  }
`;
const UserImg = styled.img`
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 16px;
`;
const Write = styled.div`
  width: 100%;
`;
const ImageWrap = styled.div`
  width: 100%;
  border-radius: 14px;
`;
const PostImage = styled.img`
  width: 100%;
  height: 100%;
  aspect-ratio: 1 / 1;
  background-position: center;
  background-repeat: no-repeat;
  object-fit: cover;
  border-radius: 14px;
`;
const PostInfo = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 12px 0;
`;

const Left = styled.div``;
const Right = styled.div``;

const Time = styled.div`
  font-size: 14px;
  color: #bdbdbd;
  margin: 12px 0;
`;
const LikeCnt = styled.span`
  font-size: 14px;
  margin-right: 12px;
  cursor: pointer;
`;
const CommentCnt = styled.span`
  font-size: 14px;
  margin-right: 12px;
`;
const BtnWrap = styled.div`
  display: flex;
  button {
    font-size: 16px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  span {
    font-size: 14px;
    color: #bdbdbd;
    padding: 0 6px;
  }
`;
const PostDesc = styled.p``;

const CommentWrap = styled.div`
  border-top: 1px solid #dbdbdb;
  padding: 12px 0;
  div {
    font-size: 14px;
  }
`;

export default DogStaDetail;
