// All.js -      

import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";

import { useInView } from "react-intersection-observer";

// 
import Cards from "../AllList/Cards";
import Loading from "../Loading";

// 
import { actionCreators as postActions } from "../../redux/modules/post";
import { actionCreators as walkActions } from "../../redux/modules/walk";

const All = () => {
  const dispatch = useDispatch();
  const allList = useSelector((state) => state.post.main); //   
  const postList = useSelector((state) => state.walk.page_all); //   

  const [pageNum, setPageNum] = useState(1);

  useEffect(() => {
    dispatch(walkActions.pageAllMD(pageNum));
  }, [pageNum]);

  // 
  const [isLoaded, setIsLoaded] = useState(false);
  const [ref, inView] = useInView();

  const getMoreItem = async () => {
    setIsLoaded(true);
    setPageNum(pageNum + 1);
    setIsLoaded(false);
  }; //   

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    //     ,   
    if (inView) {
      getMoreItem();
      // console.log("");
    }
  }, [inView]);

  useEffect(() => {
    dispatch(postActions.getAllMD());
  }, []);

  //    true, false
  // console.log(inView);

  return (
    <>
      <Wrap>
        {postList.length === 0 ? (
          "   ."
        ) : (
          <>
            {postList.map((post, index) => {
              const dogImage = post.dogImage;
              const dogName = post.dogName;
              const dogGender = post.dogGender;
              const dogAge = post.dogAge;
              const meetingDate = post.meetingDate;
              const Info = {
                dogImage,
                dogName,
                dogGender,
                dogAge,
                meetingDate,
                post,
              };

              return <Cards Info={Info} key={index} />;
            })}
            {/*    */}
            <div ref={ref}> </div>
          </>
        )}
        {/*  */}
        <div style={{ textAlign: "center", marginBottom: "20px" }}>
          {inView && postList.length !== allList.length ? <Loading /> : null}
        </div>
      </Wrap>
    </>
  );
};

const Wrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

export default All;
